import * as PIXI from "pixi.js";
import {Particle} from "./Particle";

export function practice() {

    function resize(app) {

        app.renderer.resize(window.innerWidth, window.innerHeight);
    }

    class ParticleText {
        constructor() {
            this.app = new PIXI.Application({
                autoResize: true
            });
            document.querySelector(".main").appendChild(this.app.view);

            this.particleSize = 10;
            this.particles = [];
            // this.mouse();

            this.addObjects();

            resize(this.app);

            window.addEventListener('resize', () => {
                resize(this.app);
            });

            this.animate();
        }

        addObjects() {
            this.app.loader.add('bunny', '../images/job-details-report-bg.jpg').load((loader, resources) => {

                for (let i = 0; i < 70; i++) {
                    for (let j = 0; j < 70; j++) {
                        let x = i * this.particleSize;
                        let y = j * this.particleSize;

                        let p = new Particle(x, y, resources.bunny.texture, this.particleSize);

                        this.particles.push(p);

                        this.app.stage.addChild(p.sprite);
                    }
                }



            });
        }

        animate() {
            this.app.ticker.add(() => {
                this.mouse = this.app.renderer.plugins.interaction.mouse.global;

                this.particles.forEach(p => {
                    p.update(this.mouse);
                });
            });
        }
    }

    let PT = new ParticleText();

}


